"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { ArrowLeft, Camera } from "lucide-react"
import Link from "next/link"

interface ProfileData {
  name: string
  age: string
  bio: string
  image: string
}

const sampleImages = [
  "https://images.pexels.com/photos/1858175/pexels-photo-1858175.jpeg",
  "https://images.pexels.com/photos/1382731/pexels-photo-1382731.jpeg",
  "https://images.pexels.com/photos/1642228/pexels-photo-1642228.jpeg",
  "https://images.pexels.com/photos/1391498/pexels-photo-1391498.jpeg",
]

export default function ProfilePage() {
  const [profile, setProfile] = useState<ProfileData>({
    name: "",
    age: "",
    bio: "",
    image: sampleImages[0],
  })
  const [error, setError] = useState("")
  const [success, setSuccess] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setSuccess(false)

    if (!profile.name || !profile.age || !profile.bio) {
      setError("Veuillez remplir tous les champs")
      return
    }

    const age = parseInt(profile.age)
    if (isNaN(age) || age < 18 || age > 120) {
      setError("L'âge doit être compris entre 18 et 120 ans")
      return
    }

    try {
      // In a real app, you would make an API call here to save the profile
      setSuccess(true)
      // Redirect to swipe page after successful profile creation
      // router.push("/swipe")
    } catch (err) {
      setError("Une erreur est survenue lors de la sauvegarde du profil")
    }
  }

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target
    setProfile((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  return (
    <div className="min-h-screen bg-black p-4">
      <div className="container mx-auto max-w-2xl">
        <div className="mb-6 flex items-center">
          <Link href="/swipe">
            <Button variant="ghost" size="icon" className="mr-2 text-white">
              <ArrowLeft className="h-6 w-6" />
            </Button>
          </Link>
          <h1 className="text-2xl font-bold text-white">Mon Profil</h1>
        </div>

        <form onSubmit={handleSubmit} className="space-y-6">
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
          {success && (
            <Alert className="border-green-500 bg-green-500/10 text-green-500">
              <AlertDescription>Profil mis à jour avec succès!</AlertDescription>
            </Alert>
          )}

          {/* Profile Image Selection */}
          <Card className="bg-zinc-900 text-white">
            <CardHeader>
              <CardTitle>Photo de profil</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-2 gap-4">
                {sampleImages.map((img, index) => (
                  <button
                    key={index}
                    type="button"
                    className={`relative aspect-square overflow-hidden rounded-lg border-2 ${
                      profile.image === img
                        ? "border-white"
                        : "border-transparent"
                    }`}
                    onClick={() => setProfile((prev) => ({ ...prev, image: img }))}
                  >
                    <img
                      src={img}
                      alt={`Profile ${index + 1}`}
                      className="h-full w-full object-cover"
                    />
                  </button>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Profile Information */}
          <Card className="bg-zinc-900 text-white">
            <CardHeader>
              <CardTitle>Informations personnelles</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="name">Nom complet</Label>
                <Input
                  id="name"
                  name="name"
                  value={profile.name}
                  onChange={handleChange}
                  className="bg-zinc-800"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="age">Âge</Label>
                <Input
                  id="age"
                  name="age"
                  type="number"
                  min="18"
                  max="120"
                  value={profile.age}
                  onChange={handleChange}
                  className="bg-zinc-800"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="bio">Bio</Label>
                <Textarea
                  id="bio"
                  name="bio"
                  value={profile.bio}
                  onChange={handleChange}
                  placeholder="Parlez-nous un peu de vous..."
                  className="min-h-[100px] bg-zinc-800"
                />
              </div>
            </CardContent>
          </Card>

          <Button type="submit" className="w-full bg-white text-black hover:bg-zinc-200">
            Sauvegarder le profil
          </Button>
        </form>
      </div>
    </div>
  )
}
